import React, { useState } from "react";

import FormControl from "@mui/material/FormControl";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormLabel from "@mui/material/FormLabel";


import DenseTable from './MyTable';


import Papa from "papaparse";

class Form extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      msg: "",
      selectedFile: undefined,
      isSelected: false,
      submitting: false,
      error: false,
      errorMsg: "",
      csvParsed: false,
      csvErrors: false,
      numbers: 0,
      variables: 0,
      smsSent: 0,
      smsOptout: 0,
      smsErrors: 0,
      completed: false,
      sender: "",
      csvData: undefined,
    };
    this.onMsgChange = this.onMsgChange.bind(this);
    this.onSenderChange = this.onSenderChange.bind(this);
    this.onFileChange = this.onFileChange.bind(this);
    this.sendMsg = this.sendMsg.bind(this);
    this.getData = this.getData.bind(this);
    this.onHeaderClick = this.onHeaderClick.bind(this);
  }

  onMsgChange(event) {
    this.setState({
      msg: event.target.value,
    });
  }

  onSenderChange(event) {
    this.setState({
      sender: event.target.value,
    });
  }

  onFileChange(event) {
    if (event.target.files.length > 0) {
      this.setState({
        isSelected: true,
        selectedFile: event.target.files[0],
        completed: false,
        error: false,
      });

      Papa.parse(event.target.files[0], {
        header: true,
        complete: this.getData,
      });
    }
  }


  onHeaderClick(event){
    this.setState({
      msg: this.state.msg+ '[' + event + ']',
    });
  }


  sendMsg(event) {
    console.log("send pressed");
    event.preventDefault();
    this.setState({ submitting: true, completed: false, error: false });

    const data = {
      'textmsg':this.state.msg,
      'sender':this.state.sender,
      'csvData':this.state.csvData
    }

    fetch("/csv-to-sms", {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        if (!data.status) {
          throw data.message;
        }
        this.setState({
          submitting: false,
          smsSent: data.data.sentSuccess,
          smsErrors: data.data.sentErrors,
          smsOptout: data.data.optout,
          completed: true,
        });
      })
      .catch((error) => {
        console.log(error);
        this.setState({ submitting: false, error: true, errorMsg: error });
      });
  }

  getData(result) {
    console.log("parsing complete");
    console.log(result);
    if (result.errors.length > 0) {
      this.setState({
        csvErrors: true,
        csvParsed: false,
        isSelected: false,
      });
    } else {
      this.setState({
        csvParsed: true,
        csvErrors: false,
        csvData: result.data,
        numbers: result.data.length,
        variables: result.meta.fields.length - 1,
      });
    }
  }

  csvInfos() {
    return <h1>Welcome back!</h1>;
  }

  render() {
    return (
      <FormControl fullWidth className="mb-3">
        <Button variant="contained" margin="normal" component="label">
          {this.state.isSelected ? this.state.selectedFile.name : "Upload file"}{" "}
          <input
            type="file"
            name="myFile"
            accept=".csv"
            onChange={this.onFileChange}
            hidden
          />
        </Button>

        {this.state.csvParsed ? (
          <Box mt={1}>
            <FormLabel>
              <b>{this.state.numbers}</b> numbers and{" "}
              <b>{this.state.variables}</b> variables loaded.
            </FormLabel>{" "}
          </Box>
        ) : (
          ""
        )}

        {this.state.csvErrors ? (
          <Box mt={1}>
            <FormLabel>
              Errors parsing the CSV file <b>{this.state.selectedFile.name}</b>.{" "}
            </FormLabel>{" "}
          </Box>
        ) : (
          ""
        )}

        <TextField
          id="sender"
          name="sender"
          value={this.state.sender}
          onChange={this.onSenderChange}
          margin="normal"
          required
          placeholder="Sender"
        />

        <TextField
          id="msgContent"
          name="myMsg"
          value={this.state.msg}
          onChange={this.onMsgChange}
          rows={3}
          margin="normal"
          multiline
          required
          placeholder="Message"
        />

        <Button
          variant="contained"
          onClick={this.sendMsg}
          disabled={
            !this.state.isSelected ||
            this.state.submitting ||
            this.state.msg.length == 0 ||
            this.state.sender.length == 0
          }
          margin="normal"
          component="label"
        >
          Send messages
        </Button>

        {this.state.completed ? (
          <Box mt={1}>
            <FormLabel>
              <b>{this.state.smsSent}</b> sent,  <b>{this.state.smsOptout}</b> ignored (opt-out) and <b>{this.state.smsErrors}</b>{" "}
              failed.
            </FormLabel>{" "}
          </Box>
        ) : (
          ""
        )}

        {this.state.error ? (
          <Box mt={1}>
            <FormLabel>
              Error occured during API call : {this.state.errorMsg}.
            </FormLabel>{" "}
          </Box>
        ) : (
          ""
        )}




{this.state.csvParsed ? (
          <Box mt={2}>
          <DenseTable json={this.state.csvData.slice(0, 5)} onHeaderClick={this.onHeaderClick}/>
          </Box>
        ) : (
          ""
        )}
  
      </FormControl>



    );
  }
}

export default Form;
