import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';



class DenseTable extends React.Component{

  constructor(props) {
    super(props);
    console.log(props);
  }

 
  changeHandler(event) {
    if (typeof this.props.onHeaderClick === 'function') {
        this.props.onHeaderClick(event);
   }
  }
 
  render() {
    var data = this.props.json;
    
    return (
      
      <TableContainer component={Paper}>
        <Table  size="small" >
          <TableHead>
            <TableRow>
              {Object.keys(data[0]).map((col) => (
                   <TableCell key={col} onClick={ () => this.changeHandler(col)}>{col}</TableCell>
              ))}
            </TableRow>
          </TableHead>

          <TableBody>
{data.map((row,i) => (
  <TableRow
    key={i}
    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
  >
    {Object.keys(row).map((col) => (
                   <TableCell key={row[col]} onClick={ () => this.changeHandler(col)}>{row[col]}</TableCell>
              ))}
  </TableRow>
))}
</TableBody>
        </Table>
      </TableContainer>
   
    );
  }
}

export default DenseTable;

